import Vue from 'vue'
import Router from 'vue-router'

import homePage from '@/components/homePage/homePage'
import goOut from '@/components/goOut/goOut'
import approval from '@/components/approval/approval'
import approvalDtl from '@/components/approvalDtl/approvalDtl'
import userPage from '@/components/userPage/userPage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/homePage'
    },
    {
      path: '/homePage',
      name: 'homePage',
      component: homePage,
      meta:{auth:true}
    },
    {
      path: '/goOut',
      name: 'goOut',
      component: goOut
    },
    {
      path: '/approval',
      name: 'approval',
      component: approval,
      children:[
        {
          path:':id',
          name:'approvalDtl',
          component:approvalDtl	
        }
      ]
    },
    {
      path: '/userPage',
      name: 'userPage',
      component: userPage
    },

  ]
})



